.App {
  font-family: sans-serif;
  text-align: center;
}
.wheel-wrapper{
  display: inline-flex;
}
.wheel-clickable{
  /* margin:5% */
}
.input-option {
  margin:2%
}
.JoySheet-root{
  margin:0 !important;
  height: fit-content;
  position: fixed;
}
.JoyMenu-root{
  background-color: cornsilk !important;
}

/* @media screen and (max-width: 600px) {
