#canvas-shapes{
  z-index: -1;
  filter: blur(5px);
}
.MuiFormControl-root {
  border: 0 !important  ;
}

.input-cols{
  vertical-align: middle;
  display: inline-flex;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}